body, html {
  padding: 0;
  margin: 0;
  overflow: hidden;
  font-family: "Inter", sans-serif;
}
body {height: 100vh;}
#root {height: 100%;}

#App {
  display: flex;
  flex-direction: column;
  align-content: center;
  flex-wrap: wrap;
}

#chessboard {
  position: relative;
  align-self: center;
}

#buttons {
  position: absolute;
  top: 0;
  left: 0;
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 1fr);
}

#upperArea {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

h1 {
  margin: 0;
}